.question_page .progress_question #run {
  width: 30px;
  height: 40px;
  position: absolute;
  top: 60px;
}

.question_page .progress_question #heart_wrapper {
  position: absolute;
  top: 70px;
  right: 7%;
}

.question_page .progress_question #heart_wrapper #heart {
  position: relative;
  width: 25px;
  height: 25px;
}

.question_page .progress_question #heart_wrapper #heart:before,
.question_page .progress_question #heart_wrapper #heart:after {
  position: absolute;
  content: "";
  left: 12px;
  top: 0;
  width: 12px;
  height: 20px;
  background: #b799b9;
  border-radius: 20px 20px 0 0;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  -webkit-transform-origin: 0 100%;
  transform-origin: 0 100%;
}

.question_page .progress_question #heart_wrapper #heart:after {
  left: 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  -webkit-transform-origin: 100% 100%;
  transform-origin: 100% 100%;
}

.question_page .progress_question #ground {
  position: absolute;
  width: 87%;
  height: 1.5px;
  background: black;
  top: 100px;
